@font-face {
	font-family: Arita-buriM;
	src: url(../../../fonts/Arita-buriM.otf);
}

$font-list: Arita-buriM, sans-serif;

html {
	background-color: #362113;
	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

@mixin DefaultStyle($special){
	background-color: #362113;
	border: 3px solid #83502e;
	border-radius: 5px;

	color: #CE8B54;
	font-family: inherit;
	font-weight: 500;
	font-size: 16px;
	letter-spacing: 2px;

	height: 40px;

	transition: border 0.2s ease-in-out;

	&:focus { border: 3px solid #ce8b54; outline: none; }

	@if $special == "text" {
		padding: 0 10px;
		width: 90%;
	}

	@if $special == "select" {
		display: inline;
		float: left;
		width: 50%;
		margin-left: calc(5% - 14px);
		&option { background-color: #362113; color: #CE8B54; }
	}

	@if $special == "button" {
		display: inline;
		background-color: #362113;
		padding: 0;

		float: right;
		margin-right: calc(5% - 14px);

		width: 30%;
		
		transition: border 0.2s ease-in-out, background-color 0.2s ease-in-out;

		&:hover { background-color: #714b2c; }
	}
}

#Main {
	text-align: center;
	font-family: $font-list;

	h1 { font-size: 60px; color: yellow; filter: blur(0.4px); letter-spacing: 6px; text-shadow: 1px 1px 8px #ffff9c; }

	input[type=text] { @include DefaultStyle("text"); }

	select { @include DefaultStyle("select"); }

	#advancedSetting { @include DefaultStyle("button"); }

	#pageList {
		margin: 2%;
		text-align: left;
		#List { 
			list-style-type: none; 
			padding-left: 0; 

			li {
				float: left;
				width: 33%;
				min-width: 33%;
			}
		}
	}
}

#Page {
	margin: 2%;
	height: 50vh;
	display: flex;


	background-image: linear-gradient(rgba(0,0,0, 0.5) 80%, transparent);
	background-color: rgba(0,0,0, 0.3);
	border-style: solid;
	border-radius: 0;
	border-width: 0;

	text-shadow: 0px 0px 3px #ffff9c;
	box-shadow: 2px 2px 4px rgba(0,0,0, 0.3);

	border-style: solid;
	border-width: 0.3vw 0 0 0.3vw;
	border-radius: 3px;

	#first {
		display: flex;
		flex-direction: column;
		flex: 0 0 40%;

		background-image: linear-gradient(to left, transparent 5%, rgba(0,0,0, 0.3));

		text-align: center;

		#pageName {
			display:flex;
			align-items: center;
			justify-content: center;

			flex: 0 0 30%;

			line-height: 115%;
			
			padding: 0 5%;
			font-size: 120%;
			font-weight: 1000;
			letter-spacing: 2px;
		}

		#pageImage {
			height: auto;
			width: 100%;
			z-index: 2;
			border-style: solid;
			border-width: 0.2vw 0 0.2vw 0;
			border-radius: 0.2vw;
		}

		#pageRange {
			flex: 0 0 6%;
			margin: 0.7vw 0;
			color: orange; 
			font-size: 90%;
			font-weight: 900;
			letter-spacing: 0.2vw;
		}

		#pageCost {
			flex: 1;
			margin: 0.7vw 0;
			color: #66ff00;
			font-size: 125%;
			font-weight: 900;
			letter-spacing: 0.2vw;
			text-shadow: 1px 1px 4px #66ff00;
		}
	}

	#second {
		border-style: solid;
		border-width: 0 0 0 0.1vw;
		border-radius: 0.2vw;

		color: white;

		flex: 1;
		padding: 1% 3%;

		#pageDesc {
			font-size: 75%;
		}

		#pageDice {
			margin-top: 3%;
			margin-bottom: 3%;
			vertical-align: middle;	

			#diceRoll {
				display: block; 
				float: left; 
				width: 20%;
				vertical-align: middle;
				padding-top: 2%;

				text-align: center;
				font-size: 90%;
				font-weight: 900;
			}

			#diceIcon {
				width: 13%;
				height:	13%;

				vertical-align: middle;
			}

			#diceDesc {
				font-size: 75%;
				line-height: 100%;
			}
		}
	}
}

